* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: bold;
  color: #333;
}

html {
  font-size: 62.5%; // 文字サイズをrem指定にする(1rem=10px)
  @include mq(sp) {
    font-size: 50%;
  }
}

body {
  width: 100%;
}

section {
  overflow: hidden;
}

h1 {
  margin: auto;
  font-size: map-get($font-sizes, large);
}

h2 {
  font-size: map-get($font-sizes, xx-large);
  text-align: center;
}

h3 {
  font-size: map-get($font-sizes, x-large);
  text-align: center;
}

p {
  font-size: map-get($font-sizes, medium);
}

img {
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
}

label {
  font-size: map-get($font-sizes, medium);
}

input[type='text'] {
  font-size: map-get($font-sizes, medium);
}

input[type='checkbox'] {
  display: none;
}

textarea {
  font-size: map-get($font-sizes, medium);
}
